import React, { useState } from 'react';

const Roadmap = () => {
  const [goal, setGoal] = useState('');
  const [steps, setSteps] = useState([]);

  const generateRoadmap = () => {
    const roadmapSteps = [
      'Build an emergency fund with 3-6 months of expenses.',
      `Plan and save for: ${goal}.`,
      'Explore investment opportunities for long-term wealth.',
    ];
    setSteps(roadmapSteps);
  };

  return (
    <section id="roadmap" className="section">
      <h2>Your Financial Roadmap</h2>
      <input
        type="text"
        placeholder="Your Financial Goal"
        value={goal}
        onChange={(e) => setGoal(e.target.value)}
      />
      <button onClick={generateRoadmap}>Generate Roadmap</button>
      <ul>
        {steps.map((step, index) => (
          <li key={index}>{step}</li>
        ))}
      </ul>
    </section>
  );
};

export default Roadmap;