import React, { useState, useEffect } from 'react';

const Header = () => {
  const [activeSection, setActiveSection] = useState('');
  const [isScrolled, setIsScrolled] = useState(false);

  // Highlight active section while scrolling
  useEffect(() => {
    const handleScroll = () => {
      const sections = document.querySelectorAll('section');
      const scrollPosition = window.scrollY + 200; // Offset for better detection

      sections.forEach((section) => {
        if (
          scrollPosition >= section.offsetTop &&
          scrollPosition < section.offsetTop + section.offsetHeight
        ) {
          setActiveSection(section.id);
        }
      });

      // Change header style on scroll
      setIsScrolled(window.scrollY > 50);
    };

    window.addEventListener('scroll', handleScroll);
    return () => window.removeEventListener('scroll', handleScroll);
  }, []);

  return (
    <header className={`header ${isScrolled ? 'scrolled' : ''}`}>
      <h1>Cash-First Financial Strategy</h1>
      <nav>
        <ul>
          <li>
            <a href="#overview" className={activeSection === 'overview' ? 'active' : ''}>
              Overview
            </a>
          </li>
          <li>
            <a href="#principles" className={activeSection === 'principles' ? 'active' : ''}>
              Core Principles
            </a>
          </li>
          <li>
            <a href="#tools" className={activeSection === 'tools' ? 'active' : ''}>
              Interactive Tools
            </a>
          </li>
          <li>
            <a href="#defi" className={activeSection === 'defi' ? 'active' : ''}>
              DeFi
            </a>
          </li>
          <li>
            <a href="#budget" className={activeSection === 'budget' ? 'active' : ''}>
              Budgeting
            </a>
          </li>
          <li>
            <a href="#lending" className={activeSection === 'lending' ? 'active' : ''}>
              Lending Circles
            </a>
          </li>
          <li>
            <a href="#roadmap" className={activeSection === 'roadmap' ? 'active' : ''}>
              Roadmap
            </a>
          </li>
          <li>
            <a href="#community" className={activeSection === 'community' ? 'active' : ''}>
              Community
            </a>
          </li>
          <li>
            <a href="#savings-tracker" className={activeSection === 'savings-tracker' ? 'active' : ''}>
              Savings Tracker
            </a>
          </li>
        </ul>
      </nav>
    </header>
  );
};

export default Header;