import React, { useState } from 'react';
import {
  Chart as ChartJS,
  BarElement,
  CategoryScale,
  LinearScale,
  Tooltip,
  Legend,
} from 'chart.js';
import { Bar } from 'react-chartjs-2';

// Register required components with Chart.js
ChartJS.register(CategoryScale, LinearScale, BarElement, Tooltip, Legend);

const Tools = () => {
  const [loanAmount, setLoanAmount] = useState('');
  const [interestRate, setInterestRate] = useState('');
  const [loanTerm, setLoanTerm] = useState('');
  const [results, setResults] = useState(null);
  const [error, setError] = useState('');

  const validateInputs = () => {
    if (!loanAmount || loanAmount <= 0) {
      return 'Please enter a valid loan amount.';
    }
    if (!interestRate || interestRate <= 0) {
      return 'Please enter a valid interest rate.';
    }
    if (!loanTerm || loanTerm <= 0) {
      return 'Please enter a valid loan term.';
    }
    return null;
  };

  const calculateMortgage = () => {
    const validationError = validateInputs();
    if (validationError) {
      setError(validationError);
      return;
    }

    setError(''); // Clear any previous errors
    const monthlyRate = interestRate / 100 / 12;
    const numberOfPayments = loanTerm * 12;

    const monthlyPayment =
      (loanAmount * monthlyRate) /
      (1 - Math.pow(1 + monthlyRate, -numberOfPayments));

    const totalPaid = monthlyPayment * numberOfPayments;
    const totalInterest = totalPaid - loanAmount;

    setResults({
      monthlyPayment: monthlyPayment.toFixed(2),
      totalPaid: totalPaid.toFixed(2),
      totalInterest: totalInterest.toFixed(2),
    });
  };

  return (
    <section id="tools" className="section">
      <h2>Mortgage Interest Visualizer</h2>
      <p>
        Discover how much money youre losing to interest with a traditional home
        mortgage. Adjust the loan details below to see your results.
      </p>

      <div>
        <label htmlFor="loanAmount">Loan Amount ($):</label>
        <input
          id="loanAmount"
          type="number"
          placeholder="Enter loan amount"
          value={loanAmount}
          onChange={(e) => setLoanAmount(Number(e.target.value))}
          aria-label="Loan amount"
        />

        <label htmlFor="interestRate">Annual Interest Rate (%):</label>
        <input
          id="interestRate"
          type="number"
          placeholder="Enter interest rate"
          value={interestRate}
          onChange={(e) => setInterestRate(Number(e.target.value))}
          aria-label="Interest rate"
        />

        <label htmlFor="loanTerm">Loan Term (Years):</label>
        <input
          id="loanTerm"
          type="number"
          placeholder="Enter loan term"
          value={loanTerm}
          onChange={(e) => setLoanTerm(Number(e.target.value))}
          aria-label="Loan term"
        />

        <button onClick={calculateMortgage}>Calculate</button>

        {error && <p style={{ color: 'red' }}>{error}</p>}
      </div>

      {results && (
        <div>
          <h3>Results</h3>
          <p><strong>Monthly Payment:</strong> ${results.monthlyPayment}</p>
          <p><strong>Total Paid Over Life of Loan:</strong> ${results.totalPaid}</p>
          <p><strong>Total Interest Paid:</strong> ${results.totalInterest}</p>

          <h3>Interest Visualization</h3>
          <Bar
            data={{
              labels: ['Principal', 'Interest'],
              datasets: [
                {
                  label: 'Amount in USD',
                  data: [loanAmount, results.totalInterest],
                  backgroundColor: ['#008080', '#ff5733'],
                },
              ],
            }}
            options={{
              responsive: true,
              plugins: {
                legend: { display: false },
              },
              scales: {
                y: {
                  beginAtZero: true,
                },
              },
            }}
          />
        </div>
      )}
    </section>
  );
};

export default Tools;