import React, { useState } from 'react';

const SavingsTracker = () => {
  const [goal, setGoal] = useState('');
  const [saved, setSaved] = useState('');
  const [progress, setProgress] = useState(0);

  const calculateProgress = () => {
    const percentage = ((saved / goal) * 100).toFixed(2);
    setProgress(percentage);
  };

  return (
    <section id="savings-tracker" className="section">
      <h2>Savings Progress Tracker</h2>
      <input
        type="number"
        placeholder="Savings Goal ($)"
        value={goal}
        onChange={(e) => setGoal(parseFloat(e.target.value))}
      />
      <input
        type="number"
        placeholder="Amount Saved ($)"
        value={saved}
        onChange={(e) => setSaved(parseFloat(e.target.value))}
      />
      <button onClick={calculateProgress}>Calculate Progress</button>
      {progress > 0 && <p>Your progress: {progress}%</p>}
    </section>
  );
};

export default SavingsTracker;